import React, { useState, useEffect } from 'react';
import Date from './helpers/Date';

interface Contribution {
  comment: string;
  flags: string[];
  ids: number;
  parsedcomment: string;
  size: number;
  sizediff: number;
  tags: string[];
  timestamp: string;
  title: string;
}

interface ContributionsProps {
  ucuser: string;
}

const Contributions: React.FC<ContributionsProps> = ({ ucuser }) => {
  const [contributions, setContributions] = useState<Contribution[]>([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(
          `https://en.wikipedia.org/w/api.php?action=query&origin=*&list=usercontribs&uclimit=500&ucprop=comment|parsedcomment|flags|ids|size|sizediff|tags|timestamp|title&ucuser=${ucuser}&format=json`
        );

        if (!response.ok) {
          throw new Error('Failed to fetch data');
        }

        const data = await response.json();
        const userContributions = data.query.usercontribs as Contribution[];
        setContributions(userContributions);
      } catch (error) {
        console.error(error);
      }
    };

    fetchData();
  }, [ucuser]);

  return (
    <div>
      <h2>User Contributions for {ucuser}</h2>
      <table>
        <thead>
          <tr>
            <th>Date</th>
            <th>ID</th>
            <th>Title</th>
            <th>Flags</th>
            <th>Parsed Comment</th>
            <th>Size</th>
            <th>Tags</th>
          </tr>
        </thead>
        <tbody>
          {contributions.map((contribution) => (
            <tr key={contribution.ids}>
              <td>
                <Date dateString={contribution.timestamp} />
              </td>
              <td>{contribution.ids}</td>
              <td>{contribution.title}</td>
              <td>{contribution.flags}</td>
              <td>
                <div
                  dangerouslySetInnerHTML={{
                    __html: contribution.parsedcomment,
                  }}
                />
              </td>
              <td>{contribution.size}</td>
              <td>{contribution.tags}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default Contributions;
